.login-container {
  height: 100vh;
  background-color: #282c34;
  position: relative;
}

.login-content {
  border-radius: 5px;
  width: 400px;
  height: 280px;
  background-color: rgba(255, 255, 255, 1);
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
